import React from 'react'
import './index.less'
import { Link } from 'react-router-dom'
import { Form, Icon, Input, Button, notification } from 'antd';
import { baseUrl } from '../../config';
import axios from 'axios'
class NormalLoginForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {

        }
    }
    componentDidMount() {
    }
    compareToFirstPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('password')) {
            callback('两次密码不一致');
        } else {
            callback();
        }
    };
    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
                axios.get(
                    baseUrl + '/api/logon',
                    {
                        params: {
                            email: values.username,
                            password: values.password,
                            invite_token: values.invite_code,
                            // id_seller: values.id_seller,
                        }
                    })
                    .then((Response) => {
                        console.log(Response.data);
                        let d = Response.data
                        if (d.success !== undefined) {
                            notification.open({
                                message: '注册成功',
                                description: '',
                                icon: <Icon type="smile" style={{ color: '#108ee9' }} />,
                            });
                            document.location = d.success
                        }
                        if (d.error === 0) {
                            notification['error']({
                                message: '邀请码无效',
                                description:
                                    '请重新索取一个邀请码',
                                placement: 'bottomLeft',
                            });
                        }
                    })
                    .catch((err) => {
                        console.log(err);
                    })
            }
        });
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form onSubmit={this.handleSubmit} className="login-form">
                <Form.Item>
                    {getFieldDecorator('username', {
                        rules: [{ required: true, message: '请输入用户名～' }],
                    })(
                        <Input
                            prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            placeholder="用户名"
                        />,
                    )}
                </Form.Item>
                <Form.Item>
                    {getFieldDecorator('password', {
                        rules: [{ required: true, message: '请输入密码～' }],
                    })(
                        <Input.Password
                            prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            type="password"
                            placeholder="密码"
                        />,
                    )}
                </Form.Item>
                <Form.Item hasFeedback>
                    {getFieldDecorator('confirm', {
                        rules: [
                            {
                                required: true,
                                message: '两次输入的密码需要一样！',
                            },
                            {
                                validator: this.compareToFirstPassword,
                            },
                        ],
                    })(<Input.Password
                        prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                        onBlur={this.handleConfirmBlur}
                        placeholder="重复密码"
                    />)}
                </Form.Item>

                <Form.Item>
                    {getFieldDecorator('invite_code', {
                        rules: [{ required: true, message: '请输入邀请码～' }],
                    })(
                        <Input
                            prefix={<Icon type="usergroup-add" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            placeholder="邀请码"
                        />,
                    )}
                </Form.Item>
                {/* <Form.Item>
                    {getFieldDecorator('id_seller', {
                        rules: [{ required: true, message: '请输入商家ID' }],
                    })(
                        <Input
                            prefix={<Icon type="shop" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            placeholder="商家ID"
                        />,
                    )}
                </Form.Item> */}
                <Form.Item>
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        注册
                    </Button>
                    <br></br>
                    已有账号?
                    <Link to='/admin'>登录</Link>
                </Form.Item>
            </Form>
        )
    }

}

export const Register = Form.create({ name: 'normal_login' })(NormalLoginForm);
