import React from 'react'
import './index.less'
import axios from 'axios'
import { Route, Link } from 'react-router-dom'
import { Avatar, Carousel, Spin, } from 'antd';
import Goods from './goods/index';
import { baseUrl } from '../config';
import Repos from './repos/index';
export default class Seller extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            seller_info: [],
            products: [],
        }
    }
    componentWillMount() {
        axios.get(
            baseUrl + '/api/get_seller_info',
            {
                params: {
                    id_seller: this.props.match.params.id,
                }
            })
            .then((Response) => {
                //return data数据
                console.table(Response.data);
                this.setState(Response.data)
                console.table(this.state);

            })
            .catch((err) => {
                console.log(err);
            })
        axios.get(
            baseUrl + '/api/get_products',
            {
                params: {
                    id_seller: this.props.match.params.id,
                }
            })
            .then((Response) => {
                //return data数据
                console.table('>>>res', Response.data);
                // 追加products
                this.setState({
                    products: Response.data.products,
                })
                console.table('>>>goods state', this.state);
            })
            .catch((err) => {
                console.log(err);
            })

    }
    render() {
        return (
            <div>
                <div className="header-new">
                    {this.state.seller_info.length ?
                        <div>

                            <Avatar size={64} src={this.state.seller_info[0].seller_img.includes('http') ? this.state.seller_info[0].seller_img : baseUrl + this.state.seller_info[0].seller_img || ''} className='avatar'></Avatar>
                            <h2 className="seller-name">
                                『{this.state.seller_info[0].sellerName || ''}』
                            </h2>
                        </div>
                        : <Spin />
                    }
                </div>
                {/* 轮播图 */}
                <Carousel effect="fade" autoplay>
                    {this.state.products.map(p =>
                        p.productDatas.length !== 0 ?
                            <img className='carousel-img' src={`${baseUrl}${p.productDatas[0]}`} alt='' />
                            : ''
                    )}
                </Carousel>
                {/* 商品/动态 导航栏 */}
                <div>
                    <ul className="vnavbar-container">
                        <li className='vnavbar-list'>
                            <Link to={`${this.props.match.url}`} className={this.props.location.pathname.includes('repos') ? '' : 'active'}>商品</Link>
                        </li>
                        <li className='vnavbar-list'>
                            <Link to={`${this.props.match.url}/repos`} className={this.props.location.pathname.includes('repos') ? 'active' : ''}>动态</Link>
                        </li>
                    </ul>
                </div >
                {/* 商品/动态 */}
                <div className=''>
                    <Route exact path={this.props.match.url} render={() => (
                        <Goods id={this.props.match.params.id}></Goods>
                    )} />
                    <Route path={`${this.props.match.url}/repos`} render={() => (
                        <Repos id={this.props.match.params.id} ></Repos>
                    )} />
                </div>
            </div>

        )
    }
}