import React from 'react'
import './index.less'
import { Link } from 'react-router-dom';
export default class Index extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            inpVal: ''
        }
    }
    handelChange = e => {
        this.setState({
            inpVal: e.target.value
        })
    }
    render() {
        return (
            <div className="vseller">
                <div className="seller-container">
                    <div className="seller-title">小蓝本</div>
                    <div className="seller-text">欢迎您的加入</div>
                    <div className="seller-search">
                        <input className="seller-getid" type="text" placeholder="请输入商家id" onChange={this.handelChange} />
                        <button className="seller-search-btn" onClick={this.handleSearch}>
                            <Link to={`/seller/${this.state.inpVal}`}>
                                <img src={require("../search.png")} alt="" />
                            </Link>
                        </button>
                    </div>

                    <div className="cloud1"></div>
                    <div className="cloud2"></div>
                    <div className="cloud3"></div>
                </div>
            </div >
        )
    }
}